@import '../variables.scss';

.Contact {
    
    .contact-section {
        width: 100%;
    }

    .contact-title {
        font-size: 32px;
        font-family: 'Raleway', sans-serif;
        margin-bottom: 60px;
        color: #fff;
    }

    .contact-form {

        .row:not(:last-child) {
            margin-bottom: 10px;
        }

        .form-control {
            background: transparent;
            border: none;
            border-bottom: 1px solid #fff;
            border-radius: 0;
            color: #fff;

            &:focus {
                box-shadow: none;
            }

        }

        input.form-control {
            min-height: 50px;
        }

        textarea.form-control {
            min-height: 150px;
        }

        .submit-btn {
            cursor: pointer;
            font-family: 'Raleway', sans-serif;
            transition: all .25s ease-in-out;
            padding: 10px 20px;
            border: none;
            background-color: $secondary-light;
            color: #fff;
            font-weight: bold;
            transition: background-color .25s ease-in-out;

            &:hover {
                background-color: $secondary-dark;
            }
        }

        .form-group {
            position: relative;
            padding-bottom: 30px;
    
            .text-validator {
                position: absolute;
                left: 0;
                bottom: 0;
                color: #dc3545;
                margin-top: 5px;
            }
        }
    }    
}

@media (max-width: 576px) {

    .Contact {
        .contact-title {
            font-size: 28px;
            font-family: 'Raleway', sans-serif;
            margin-bottom: 60px;
            color: #fff;
        }
    }
}